@import url(https://fonts.googleapis.com/css?family=Khula:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{display:flex;justify-content:space-between;width:100%;font-size:2em;background:#3f3f3f;color:white}.header-title{margin:0.5em}.counter button{color:white;background:none;font-size:.5em;border:none}

body{background:#111}.hidden{opacity:0}.console-container{font-family:Khula;font-size:4em;text-align:center;height:200px;width:600px;display:block;position:absolute;color:white;top:0;bottom:0;left:0;right:0;margin:auto}.console-underscore{display:inline-block;position:relative;top:-0.14em;left:10px}




